 }
 
 #call-to-action h2 {
    font: 28px/36px 'opensans-bold', sans-serif;
    color: #EBEEEE;
    margin-bottom: 6px;
 }
 
 #call-to-action h2 a {
    color: inherit;
 }
 
 #call-to-action p {
    color: #636363;
    font-size: 17px;
 }
 
