    text-transform: uppercase;
    letter-spacing: 3px;
    color: #fff;
    background: #0D0D0D;
    border: none;
    cursor: pointer;
    height: auto;
    display: inline-block;
    border-radius: 0px;
    margin-left: 26%;
 }
 
 #contact button.submit:hover {
    color: #0D0D0D;
    background: #fff;
 }
 
 #contact span.required {
    color: #0762f9;
    font-size: 13px;
 }
 
 #message-warning,
 #message-success {
    display: none;
    background: #0F0F0F;
    padding: 24px 24px;
    margin-bottom: 36px;
    width: 65%;
    margin-left: 26%;
 }
 
