    color: #D72828;
 }
 
 #message-success {
    color: #0762f9;
 }
 
 #message-warning i,
 #message-success i {
    margin-right: 10px;
 }
 
 #image-loader {
    display: none;
    position: relative;
    left: 18px;
    top: 17px;
 }
 
 
